.fl {
  float: left;
}
.fr {
  float: right;
}
.wrapper {
  width: 100%;
  min-width: 1506px;
}
.g-wrap {
  margin: 0 auto;
  position: relative;
  height: 100%;
}
.video-player {
  z-index: 99999;
  position: fixed;
  top: 0;
  width: 100%;
  bottom: 0;
  background: #000;
  .video-tag {
    width: 100%;
    height: 100%;
    display: block;
  }
  .close {
    width: 30px;
    height: 30px;
    background: url('../images/close-video.png') no-repeat center;
    background-size: 30px;
    position: absolute;
    right: 30px;
    top: 30px;
  }
}