.g-hd {
  position: fixed;
  z-index: 99999;
  width: 100%;
  height: 120px;
  .top-nav {
    margin: 0 auto;
    width: 1506px;
    min-width: 1200px;
    height: 100%;
    li {
      font-size: 18px;
      float: left;
      list-style: none;
      margin-right: 45px;
      line-height: 120px;
    }
    .logo a{
      display: block;
      margin-top: 24px;
      background-image: url('../images/logo_w.png');
      height: 72px;
      width: 153px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100% 100%
    }
  }
}