/*reset*/
html {
  font-family: "PingFang SC", "Helvetica Neue", Helvetica, "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #fff;
}
html * {
  outline: 0;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  margin: 0;
  font-size: 14px;
  line-height: 1.5;
  color: #333;
  background-color: #fff;
  word-break: break-word;
}
a {
  text-decoration: none;
  color: #c8caca;
  transition: color 0.3s ease-out;
}
a,
a:hover {
  color: #fff;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
  vertical-align: middle;
  width: 100%;
}
pre {
  overflow: auto;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 0.1em;
}
i {
  color: #bbb;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
input[disabled] {
  cursor: default;
}
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input:focus {
  outline: none;
}
input[type="checkbox"],
input[type="radio"] {
  padding: 0;
}
input[type="text"],
input[type="search"],
input[type="tel"],
input[type="password"] {
  -webkit-appearance: none;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
textarea {
  overflow: auto;
  resize: none;
  -webkit-appearance: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
figure,
form,
blockquote {
  margin: 0;
  font-weight: normal;
}
ul,
ol,
li,
dl,
dd,
option {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none outside none;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  color: #aaa;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #aaa;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.wrapper {
  width: 100%;
  min-width: 1506px;
}
.g-wrap {
  margin: 0 auto;
  position: relative;
  height: 100%;
}
.video-player {
  z-index: 99999;
  position: fixed;
  top: 0;
  width: 100%;
  bottom: 0;
  background: #000;
}
.video-player .video-tag {
  width: 100%;
  height: 100%;
  display: block;
}
.video-player .close {
  width: 30px;
  height: 30px;
  background: url('../images/close-video.png') no-repeat center;
  background-size: 30px;
  position: absolute;
  right: 30px;
  top: 30px;
}
.g-hd {
  position: fixed;
  z-index: 99999;
  width: 100%;
  height: 120px;
}
.g-hd .top-nav {
  margin: 0 auto;
  width: 1506px;
  min-width: 1200px;
  height: 100%;
}
.g-hd .top-nav li {
  font-size: 18px;
  float: left;
  list-style: none;
  margin-right: 45px;
  line-height: 120px;
}
.g-hd .top-nav .logo a {
  display: block;
  margin-top: 24px;
  background-image: url('../images/logo_w.png');
  height: 72px;
  width: 153px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
.g-ft {
  background-color: #666464;
  overflow: hidden;
  min-width: 1200px;
}
.g-ft .footer-content {
  margin: 0 auto;
  padding-bottom: 105px;
  width: 1506px;
  height: 100%;
  overflow: hidden;
  border-bottom: solid 1px #c8caca;
}
.g-ft .footer-content .tel {
  width: 230px;
  height: 40px;
  background-image: url('../images/tel.png');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
.g-ft .footer-content .index-button {
  margin-top: 20px;
  text-align: center;
  height: 50px;
  line-height: 50px;
  background-color: #7e318e;
  border-radius: 3px;
  font-size: 20px;
  font-weight: bold;
}
.g-ft .footer-content .fl,
.g-ft .footer-content .fr {
  margin-top: 60px;
}
.g-ft .footer-content .fl li,
.g-ft .footer-content .fr li {
  float: left;
  color: #fff;
  margin-right: 100px;
}
.g-ft .footer-content .fl li h4,
.g-ft .footer-content .fr li h4 {
  margin-bottom: 45px;
  font-weight: bold;
  font-size: 21px;
}
.g-ft .footer-content .fl li a,
.g-ft .footer-content .fr li a {
  margin-bottom: 10px;
  display: block;
  color: #c8caca;
  font-size: 18px;
}
.g-ft .footer-content .fl li p,
.g-ft .footer-content .fr li p {
  color: #c8caca;
  margin-bottom: 10px;
  font-size: 18px;
}
.g-ft .footer-content.bottom li {
  margin-top: 25px;
}
.g-ft .footer-content.bottom li a {
  padding-right: 45px;
  font-size: 18px;
  color: #c8caca;
}
.g-ft .footer-content.bottom h4 {
  margin-top: 40px;
  padding-bottom: 12px;
  font-weight: bold;
  font-size: 21px;
  color: #fff;
  display: inline-block;
  border-bottom: solid 2px #7e318e;
}
body {
  min-height: 100%;
  height: 100%;
}
.wrapper {
  width: 1506px;
  height: 100%;
}
.course-sys {
  width: 1506px;
  height: 900px;
  margin: 0 auto;
  text-align: center;
  background: pink;
}
.system-title {
  font-size: 45px;
  color: #727171;
}
.pic {
  width: 100%;
  height: auto;
}
